import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { resolve } from 'dns';
import { VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-list-address-customer-ghn',
  templateUrl: './list-address-customer-ghn.component.html',
  styleUrls: ['./list-address-customer-ghn.component.scss']
})
export class ListAddressCustomerGhnComponent implements OnInit {
  @Input("visible_list_address") visible_list_address: boolean = false;
  @Output("onCloseListAddress") onCloseListAddress = new EventEmitter<any>();
  @Input("id_customer") id_customer;
  @Input("delivery") delivery;
  constructor(
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private nzModal: NzModalService
  ) { }
  infoAddress: Array<any> = [];
  ngOnInit() {
    this.getData();
  }
  getData() {
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      this.vhQuerySales.refreshLocalAddresss().then(() => {
        this.vhComponent.hideLoading(0)
        this.infoAddress = this.vhQuerySales.getlocalAddresss().filter(item => item.type == this.delivery.type && item.id_customer == this.id_customer).reverse();
        
      })
    })
  }
  chooseAddress(item) {
    this.nzModal.confirm({
      nzTitle: `<b>${this.lang.translate("Confirm")}?</b>`,
      nzContent: `<i>${this.lang.translate("Do you want to use this address ?")}</i>`,
      nzOnOk: () => {
        return new Promise<any>(resolve => {
          this.vhQuerySales.updateAddress(item._id, { date: new Date().toISOString() }).then((res) => {
            if (res) this.onCloseListAddress.emit(item);
            resolve(true)
          }).catch(err => { })
        })
      },
      nzOnCancel: () => { }
    });
  }
  onClose() {
    this.onCloseListAddress.emit(false);
  }
  openAddAddress() {
    this.onCloseListAddress.emit({ add: true })
  }
  closeAndGetValueAddAddress(event) {
    this.onCloseListAddress.emit({ add: false })
  }
  deleteAddressGHN(item) {
    this.nzModal.confirm({
      nzTitle: `<b>${this.lang.translate("Confirm")}?</b>`,
      nzContent: `<i>${this.lang.translate("Do you want to delete address ?")}</i>`,
      nzOnOk: () => {
        return new Promise<any>(resolve => {
          this.vhQuerySales.deleteAddress(item._id).then((res) => {
            this.infoAddress.splice(this.infoAddress.findIndex(data => data._id == item._id), 1)
            resolve(true)
          }).catch(err => { })
        })
      },
      nzOnCancel: () => { }
    });
  }
}
