import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { NzCarouselComponent } from 'ng-zorro-antd/carousel';
import { VhType } from 'src/app/sales/interface/vh-type';

@Component({
  selector: 'app-best-selling',
  templateUrl: './best-selling.component.html',
  styleUrls: ['./best-selling.component.scss']
})
export class BestSellingComponent implements OnInit {
  @ViewChild("success1", { static: false }) myCarousel: NzCarouselComponent;
  @ViewChild("success2", { static: false }) myCarouse2: NzCarouselComponent;
  @Output("chooseBestSelling") chooseBestSelling = new EventEmitter<any>();
  @Input() default_price_type: any;
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  auto_hidden_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_hidden_lot
  auto_select_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_select_lot
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales
  ) {
  }
  public products: any[] = [];
  public searchList: any = [];
  public typeGoods = 0;
  serviceList: any[] = [];
  serviceBest: any[] = [];
  ngOnInit(): void {
    this.getData();
  }
  getData() {
    Promise.all([
      this.configProductsSelling(),
      this.configServicesSelling()
    ]).then(()=>{
    })

  }

  // x l sn phm cho php bn nhanh
  configProductsSelling(){
    this.products = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(good => good.selling && !good.delete_hidden).slice(0,12).map(item => {
      let unit = this.vhQuerySales.getUnit_byRatio(item.units, item.units.find(e => e.default) ? item.units.find(e => e.default).ratio : 1) 
      if (item.type == VhType.PRODUCT && item.manylot) {
        item.lots = this.vhAlgorithm.sortDatebyASC(item.lots, 'date_exp')
        if (this.auto_hidden_lot) item.lots = item.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime())
        if (this.auto_select_lot && item.lots.length) {
          let min_date_exp = this.vhAlgorithm.sortDatebyASC(item.lots.filter(e => new Date(e.date_exp).getTime() >= new Date().getTime()), 'date_exp')
          // 
          // 
          if (min_date_exp.length) {
            item['id_lotproduct'] = min_date_exp[0]._id;
            item['lot_number'] = min_date_exp[0].lot_number;
          }
        }
      }
      return { ...item, ...unit }
    });
    let j = 0;
    // this.products.forEach(element=>{
    //   if()
    // })
    for (let i = 0; i < this.products.length; i++) {
      if (!this.searchList[j])
        this.searchList[j] = this.searchList.length
        ? new Array(this.products[j + 5])
        : new Array(this.products[j])
      else {
        this.searchList[j].push(this.products[i])
      }
      if (this.searchList[j].length == 6) j++;
    }
  }

  // x l dch v cho php bn nhanh
  configServicesSelling(){
    this.serviceList = this.vhQuerySales.getlocalDetailProducts(VhType.SERVICE_SERVICETIME).filter(good =>good.selling && !good.delete_hidden).slice(0,12).map(item => {return {...item,...item.units[0]}});
    let k = 0;
    for (let i = 0; i < this.serviceList.length; i++) {
      if (!this.serviceBest[k]) this.serviceBest[k] = this.serviceBest.length ? new Array(this.serviceList[k + 5]) : new Array(this.serviceList[k])
      else {
        this.serviceBest[k].push(this.serviceList[i])
      }
      if (this.serviceBest[k].length == 6) k++;
    }
  }

  chooseProduct(item) {
    this.chooseBestSelling.emit(item);
  }
  changeTypeGood(event) {
    this.typeGoods = event;
  }
  nzPageIndexChange(event) {
    if (this.typeGoods == 0) this.myCarousel.goTo(event - 1);
    else if (this.typeGoods == 1) this.myCarouse2.goTo(event - 1);
  }
  array = [1, 2, 3, 4];
  effect = 'scrollx';
  ngAfterViewChecked() {
    if (document.querySelector(".ant-tabs-nav") && document.querySelector(".sales-best-selling")) {
      let height = document.querySelector(".sales-best-selling").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - 20 + "px";
      if (document.getElementById("carousel-product")) document.getElementById("carousel-product").style.height = height;
      if (document.getElementById("carousel-service")) document.getElementById("carousel-service").style.height = height;
    }
  }
}
