import { VhQuerySales } from 'ionic-vhframeworks';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CountryCode } from 'libphonenumber-js/max';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-customer-profile',
  templateUrl: './customer-profile.component.html',
  styleUrls: ['./customer-profile.component.scss']
})
export class CustomerProfileComponent implements OnInit {
  @Input("visibleInfoCustomer") visibleInfoCustomer: boolean = false;
  @Input("customer") customer;
  @Output("closeInfoCustomer") closeInfoCustomer = new EventEmitter<any>();
  formEditCustomer: FormGroup;



  constructor(
    private vhQuerySales: VhQuerySales,
    private functionService: FunctionService,
    private vhComponent: VhComponent,
    private lang: LanguageService,
  ) { }



  ngOnInit() {
    this.initaddCustomerForm();
  }



  goBack() {
    this.closeInfoCustomer.emit(false)
  }



  initaddCustomerForm() {
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.customer.phone, this.countryCode as CountryCode);
    this.formEditCustomer = new FormGroup({
      img: new FormControl(this.customer.img),
      name: new FormControl(this.customer.name, Validators.compose([Validators.required])),
      email: new FormControl(this.customer.email, Validators.compose([Validators.email])),
      gender: new FormControl(this.customer.gender),
      datebirth: new FormControl(this.customer.datebirth),
      phone: new FormControl(this.formattedPhone, [
        Validators.required,
        this.functionService.phoneValidator(() => this.countryCode),
      ]),
      address: new FormControl(this.customer.address),
      country: new FormControl(this.customer.country),
      province: new FormControl(this.customer.province),
      district: new FormControl(this.customer.district),
      note: new FormControl(this.customer.note),
      debt_enable: new FormControl(this.customer.debt_enable),
      _id: new FormControl(this.customer._id),
      isBusiness: new FormControl(this.customer.isBusiness),
      taxcode: new FormControl(this.customer.taxcode),
    });
    // taxcode required khi isBusiness = true
    if(this.customer.isBusiness) {
      this.formEditCustomer.get('taxcode').setValidators([Validators.required]);
      this.formEditCustomer.get('taxcode').updateValueAndValidity();
    }
  }



  save() {
    let customer: any = { ...this.formEditCustomer.value };

    const phoneNumberValue = customer.phone;
    customer.countryCode = this.countryCode;
    customer.phone = this.functionService.getNationalPhoneNumber(phoneNumberValue, this.countryCode);
    customer.phoneNumber = this.functionService.getInternationalPhoneNumber(phoneNumberValue, this.countryCode);

    if(customer.isBusiness) {
      delete customer.gender;
      delete customer.datebirth;
    }
    else {
      delete customer.taxcode;
    }

    this.vhQuerySales.updateCustomer(this.customer._id, customer).then((res: any) => {
      if(res.vcode == 0) {
        this.closeInfoCustomer.emit(true);
      }
      else if(res.vcode == 1) {
        //This phone number is already in use with another customer
        this.vhComponent.hideLoading(0);
        this.vhComponent.showToast(1500, this.lang.translate("This phone number is already in use with another customer"), 'alert-toast');
      }
    });
  }
  
  
  
  /** Kim tra s in thoi  tn ti ha cha  hin th thng bo */
  isExitsPhone: boolean = false;

  /** M quc gia */
  countryCode: string = this.functionService.defaultCountryCode;
  
  /** Calling code */
  callingCode: string = this.functionService.defaultCallingCode;
  
  /** S in thoi  format. VD: 333 333 333 */
  formattedPhone: string = '';



  /**
   * M modal chn m quc gia
   */
  openSearchCountryCode() {
    this.functionService.openSearchCountryCode().then((data) => {
      this.countryCode = data.code;
      this.callingCode = data.callingCode;
      this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.formEditCustomer.value.phone, this.countryCode as CountryCode);
      this.formEditCustomer.get('phone')?.markAsTouched();
      this.formEditCustomer.get('phone')?.markAsDirty();
      this.formEditCustomer.get('phone')?.updateValueAndValidity();
    }).catch((error) => {
      console.error('Error opening country code search:', error);
    });
  }



  /**
   * Format s in thoi khi nhp v set vo form
   * @param event S kin khi nhp s in thoi
   */
  formatPhoneNumber(event: Event): void {
    // Ly s in thoi  format
    const input = (event.target as HTMLInputElement).value;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(input, this.countryCode as CountryCode);
    // Cp nht vo form
    this.formEditCustomer.get('phone')?.setValue(this.formattedPhone, { emitEvent: false });
  }

}
