import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { AhamoveService } from 'src/app/sales/menu/manage/delivery/services/ahamoveService/ahamove.service';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { GhtkService } from 'src/app/sales/menu/manage/delivery/services/ghtkService/ghtk.service';
import { ViettelService } from 'src/app/sales/menu/manage/delivery/services/viettelService/viettel.service';
import { VnpostService } from 'src/app/sales/menu/manage/delivery/services/vnpostPost/vnpost-service.service';
import { LanguageService } from 'src/app/sales/services/language.service';
const GHNTOKEN: string = '656776fd-1e78-11eb-aaa9-3e267f98f494'
@Component({
  selector: 'app-check-fees',
  templateUrl: './check-fees.component.html',
  styleUrls: ['./check-fees.component.scss']
})
export class CheckFeesComponent implements OnInit {
  @Input('id_delivery') id_delivery;
  @Input('visibleCheckFees') visibleCheckFees = false
  @Output('hideModalCheckFees') hideModalCheckFees = new EventEmitter<any>()
  @Input('id_customer') id_customer: string
  delivery: any = {}
  isLoading: boolean = false
  listProvinceGHN: Array<any> = []
  listDistrictGHN: Array<any> = []
  listWardGHN: Array<any> = []
  serviceGHN: Array<any> = []
  listProvinceViettel: Array<any> = []
  listDistrictViettel: Array<any> = []
  listWardViettel: Array<any> = []
  listServiceViettel: Array<any> = []
  listServiceAddViettel: Array<any> = []
  MONEY_COLLECTION: any
  PRODUCT_PRICE: any
  dataAddress: any = { ProvinceID: '', ProvinceName: '', DistrictID: '', DistrictName: '', WardCode: '', WardName: '' }
  dataProduct: any = { height: '', weight: '', length: '', width: '' }
  resultFeesGHN: Array<any> = []
  resultFeesGHTK: Array<any> = []
  resultFeesAhamove: Array<any> = []
  resultFeesVnpost: Array<any> = []
  listAddressCustomer: Array<any> = []
  infoCus: any = {}
  loadingDone: boolean = false;
  constructor(
    public vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private ghnService: GhnService,
    private ghtkService: GhtkService,
    private ahamoveService: AhamoveService,
    private vnPost: VnpostService,
    private viettelService: ViettelService,
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm,
  ) { }

  ngOnInit(): void {
    this.getData()
  }
  getData() {
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      Promise.all([this.vhQuerySales.refreshLocalDeliverys(), this.vhQuerySales.refreshLocalAddresss()]).then(() => {
        this.delivery = this.vhQuerySales.getlocalDelivery(this.id_delivery)
        switch (this.delivery.type) {
          case 1: this.getDataGHN(); break
          case 2: this.getDataGHTK(); break
          case 3: this.getDataAhamove(); break
          case 4: this.getDataVnpost(); break
          case 5: this.getDataViettelPost(); break
        }
      })
    })
  }
  //Ly d liu giao hng nhanh
  getDataGHN() {
    this.ghnService.connectTokenGhn(this.delivery.token).then((resultShop: any) => {
      this.vhComponent.hideLoading(0)
      if (resultShop.code == 200) {
        let shops = resultShop.data.shops.find(item => item._id == this.delivery.shop_id);
        this.delivery = {
          ...this.delivery, name: shops.name, phone: shops.phone, address: shops.address,
          district_id: shops.district_id, ward_code: shops.ward_code, logo: './assets/icon/management/deliverys/1.svg'
        }
        if (this.id_customer != 'id_retail') {
          this.infoCus = { ...this.vhQuerySales.getlocalCustomer(this.id_customer) }
          this.getListAddressCustomer()
        }
      } else this.messageError()
    }).catch(err => { this.messageError(), this.vhComponent.hideLoading(0) })
  }
  //Ly d liu giao hng tit kim
  getDataGHTK() {
    this.ghtkService.getWareHouseGhtk(this.delivery.token).then((res: any) => {
      this.vhComponent.hideLoading(0)
      let shops = res.data.find(item => item.pick_address_id == this.delivery.shop_id);
      let data = this.renderAddressGHTK(shops.address);
      this.delivery = {
        ...this.delivery, pick_name: shops.pick_name, pick_address: data.pick_address,
        pick_province: data.pick_province, pick_district: data.pick_district, logo: './assets/icon/management/deliverys/2.svg'
      }
      if (this.id_customer != 'id_retail') {
        this.infoCus = { ...this.vhQuerySales.getlocalCustomer(this.id_customer) }
        this.getListAddressCustomer()
      }
    }).catch(err => { this.messageError(), this.vhComponent.hideLoading(0) })
  }
  //ly d liu giao hng ahamove
  getDataAhamove() {
    this.ahamoveService.getTokenAhaMove(this.delivery.phone).then((res: any) => {
      this.vhComponent.hideLoading(0)
      this.delivery.token = res.token
      let addresss: Array<any> = this.vhQuerySales.getlocalAddresss().filter(item => item.id_delivery == this.delivery._id && item.type == 4)
      let pickAddress = addresss.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
      this.delivery = { ...pickAddress, ...this.delivery, logo: './assets/icon/management/deliverys/3.svg' }
      if (this.id_customer != 'id_retail') {
        this.infoCus = { ...this.vhQuerySales.getlocalCustomer(this.id_customer) }
        this.getListAddressCustomer()
      }
    }).catch(err => { this.messageError(), this.vhComponent.hideLoading(0) })
  }
  getDataVnpost() {
    this.vhComponent.hideLoading(0)
    let addresss: Array<any> = this.vhQuerySales.getlocalAddresss().filter(item => item.id_delivery == this.delivery._id && item.type == 5)
    let pickAddress = addresss.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
    this.delivery = { ...pickAddress, ...this.delivery, logo: './assets/icon/management/deliverys/4.svg' }
    if (this.id_customer != 'id_retail') {
      this.infoCus = { ...this.vhQuerySales.getlocalCustomer(this.id_customer) }
      this.getListAddressCustomer()
    }
  }
  getDataViettelPost() {
    this.viettelService.getListInventory(this.delivery.token).then((result: any) => {
      this.vhComponent.hideLoading(0)
      if (!result.error) {
        let store = result.data.find(item => item.groupaddressId == this.delivery.GROUPADDRESS_ID)
        
        this.delivery = { ...store, ...this.delivery, logo: './assets/icon/management/deliverys/5.svg' }

        if (this.id_customer != 'id_retail') {
          this.infoCus = { ...this.vhQuerySales.getlocalCustomer(this.id_customer) }
          this.getListAddressCustomer()
        }
        this.vhAlgorithm.waitingStack().then(() => {
          this.MONEY_COLLECTION = this.vhAlgorithm.vhnumeral(".MONEY_COLLECTION")
          this.PRODUCT_PRICE = this.vhAlgorithm.vhnumeral(".PRODUCT_PRICE")
        })
      }
      
    }).catch(err => { this.messageError(), this.vhComponent.hideLoading(0) })
  }
  renderAddressGHTK(address) {
    let pick_address: any = {};
    let indexProvince = address.lastIndexOf(",");
    let indexDistrict = address.lastIndexOf(",", indexProvince - 1);
    pick_address.pick_province = address.slice(indexProvince + 1).trim();
    pick_address.pick_district = address.slice(indexDistrict + 1, indexProvince).trim();
    pick_address.pick_ward = address.slice(0, indexDistrict).trim();
    pick_address.pick_address = pick_address.pick_ward;
    return pick_address;
  }
  messageError() {
    this.vhComponent.alertMessageDesktop('error', this.lang.translate('An error occured while getting transport'))
  }
  hideModal() {
    this.visibleCheckFees = false
    this.hideModalCheckFees.emit(false)
  }
  getProvinceGHN() {
    this.isLoading = true
    this.ghnService.getProvinceGhn(GHNTOKEN).then((res: any) => {
      this.isLoading = false
      if (res.code == 200 && this.delivery.type != 3)
        this.listProvinceGHN = res.data
      else if (res.code == 200 && this.delivery.type == 3)
        this.listProvinceGHN = res.data.filter(item => item.ProvinceID == 201 || item.ProvinceID == 202)

    }).catch(err => { this.messageError(); this.isLoading = false })
  }
  /**
   * S kin thay i tnh => reset huyn x
   * @param event 
   */
  changeProvinceGHN(event: any) {
    if (event) {
      this.listDistrictGHN = []
      this.listWardGHN = []
      this.dataAddress['WardCode'] = ''
      this.dataAddress['DistrictID'] = ''
      if (this.delivery.type == 2 || this.delivery.type == 3) {
        let province = this.listProvinceGHN.find(item => item.ProvinceID == event)
        this.dataAddress['ProvinceName'] = province.ProvinceName
      }
    }
  }
  /**
   * Ly danh sch huyn
   */
  getDistrictGHN() {
    this.isLoading = true
    this.ghnService.getDistrictGhn(GHNTOKEN, this.dataAddress['ProvinceID']).then((res: any) => {
      this.isLoading = false
      if (res.code == 200) this.listDistrictGHN = res.data
    }).catch(err => { this.messageError(); this.isLoading = false })
  }
  /**
   * S kin thay i a ch huyn => reset x
   * @param event 
   */
  changeDistrictGHN(event: any) {
    if (event) {
      this.dataAddress['WardCode'] = ''
      this.dataAddress['DistrictID'] = event
      if (this.delivery.type == 2 || this.delivery.type == 3) {
        let district = this.listDistrictGHN.find(item => item.DistrictID == event)
        this.dataAddress['DistrictName'] = district.DistrictName
      }
    }
  }
  /**
   * Ly danh sch x, phng
   */
  getWardGHN() {
    this.isLoading = true
    this.ghnService.getWardGhn(GHNTOKEN, this.dataAddress['DistrictID']).then((res: any) => {
      this.isLoading = false
      if (res.code == 200) this.listWardGHN = res.data
    }).catch(err => { this.messageError(); this.isLoading = false })
  }
  /**
   * Sau khi chn xong x phng, dng api get v cc dch v h tr(i b, my bay...)
   * @param event 
   */
  changeWardGHN(event: any) {
    this.serviceGHN = []
    if (event && this.delivery.type == 1) {
      let json: any = {
        shop_id: this.delivery.shop_id,
        from_district_id: this.delivery.district_id,
        to_district_id: this.dataAddress.DistrictID
      }
      this.ghnService.getServiceDelivery(this.delivery.token, json).then((res: any) => {
        this.serviceGHN = res.data.filter(item => {
          return item.service_type_id == 1 || item.service_type_id == 2
        });
      }).catch(err => { this.messageError() })
    } else if (event && this.delivery.type == 3) {
      let ward = this.listWardGHN.find(item => item.WardCode == event)
      this.dataAddress['WardName'] = ward.WardName
    }
  }
  /**
    * Hm tr v thng tin  chun b cho vo cc hm getService
  **/
  prepareInfo(service_id) {
    return {
      shop_id: this.delivery.shop_id, to_district_id: this.dataAddress.DistrictID,
      to_ward_code: this.dataAddress.WardCode,
      height: this.dataProduct.height, length: this.dataProduct.length, weight: this.dataProduct.weight, width: this.dataProduct.width,
      insurance_value: 0, service_id: service_id,
      from_district_id: this.delivery.district_id, from_ward_code: this.delivery.ward_code
    };
  }
  /**
   * Nu type = 1 l ghn,
   * Type = 2 l ghtk
   * Type = 3 l ahamove
   */
  viewResult(type) {

    switch (type) {
      case 1: this.viewResultGHN(); break;
      case 2: this.viewResultGHTK(); break;
      case 3: this.viewResultAhamove(); break;
      case 4: this.viewResultVnpost(); break;
    }
  }
  viewResultGHN() {
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      let process: Array<any> = []
      this.serviceGHN.forEach(item => process.push(this.ghnService.calculate_delivery_fee(this.delivery.token, this.prepareInfo(item.service_id))))
      Promise.all(process).then((result: Array<any>) => {
        this.vhComponent.hideLoading(0)
        this.resultFeesGHN = result.map((item, index) => {
          return { ...item.data, short_name: this.serviceGHN[index]?.short_name }
        })
      }).catch(err => { this.messageError(); this.vhComponent.hideLoading(0) })
    })
  }
  viewResultGHTK() {
    this.resultFeesGHTK = []
    let json: any = {
      shop_id: this.delivery.shop_id, province: this.dataAddress.ProvinceName, token: this.delivery.token,
      district: this.dataAddress.DistrictName, weight: this.dataProduct.weight / 1000, insurance_value: 0,
    }
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      Promise.all([
        this.ghtkService.calculateFeeGhtk(json, 'road'),
        this.ghtkService.calculateFeeGhtk(json, 'fly')
      ]).then((result: Array<any>) => {
        result.forEach((item, index) => {
          if (item.fee?.delivery && index == 0)
            this.resultFeesGHTK.push({ ...item.fee, short_name: 'Standard' })
          if (item.fee?.delivery && index == 1 && item.fee.fee != this.resultFeesGHTK[0].fee)
            this.resultFeesGHTK.push({ ...item.fee, short_name: 'Fast' })
        })
        this.vhComponent.hideLoading(0)
      }).catch(err => { this.vhComponent.hideLoading(0); this.messageError() })
    })

  }
  services_SGN: Array<any> = [{ "_id": "SGN-BIKE" }, { "_id": "SGN-EXPRESS" }, { "_id": "SGN-POOL" }, { "_id": "SGN-SAMEDAY" }, { "_id": "SGN-TRICYCLE" }, { "_id": "SGN-TRUCK-500" }, { "_id": "SGN-TRUCK-2000" }]
  services_HAN: Array<any> = [{ "_id": "HAN-BIKE" }, { "_id": "HAN-EXPRESS" }, { "_id": "HAN-POOL" }, { "_id": "HAN-SAMEDAY" }];
  prepareDataCalculate() {
    const data = {
      token: this.delivery.token,
      services: this.dataAddress.ProvinceID == 202 ? this.services_SGN : this.services_HAN,
      path: [
        { address: this.formatAddressAhamove(this.delivery.address, this.delivery.ward, this.delivery.district, this.delivery.province) },
        { address: this.formatAddressAhamove(this.dataAddress.address, this.dataAddress.WardName, this.dataAddress.DistrictName, this.dataAddress.ProvinceName), cod: 0 }
      ]
    }
    return data;
  }
  /**Format a ch ca ahamove */
  formatAddressAhamove(address, ward, district, province) {
    return address + ", " + ward + ", " + district + ", " + province;
  }
  viewResultAhamove() {
    this.resultFeesAhamove = []
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.ahamoveService.calculateFeeAhamove(this.prepareDataCalculate()).then((res: any) => {
        this.vhComponent.hideLoading(0)
        this.loadingDone = true
        res.forEach(item => {
          if (item.total_fee && item.total_fee >= 0) {
            let data = this.filterTitleServiceAhamove(item._id);
            this.resultFeesAhamove.push({ short_name: data.title, description: data.description, ...item })
          }
        })
      }).catch(err => { this.messageError(); this.vhComponent.hideLoading(0) })
    })
  }
  filterTitleServiceAhamove(id) {
    let title = '';
    let description = '';
    switch (id) {
      case 'SGN-BIKE': title = 'Over speed'; description = 'Deliver within an hour'; break;
      case 'SGN-EXPRESS': title = 'Near delivery'; description = 'Super fast delivery only 30 minutes'; break;
      case 'SGN-POOL': title = 'Dirt cheap'; description = 'Save 20%, 2-hour delivery'; break;
      case 'SGN-SAMEDAY': title = '4-Hour'; description = 'Delivery within the city, committed in 4 hours'; break;
      case 'SGN-TRICYCLE': title = 'Delivery tricycle'; description = 'Tricycle service description'; break;
      case 'SGN-TRUCK-500': title = '500kg truck'; description = 'Delivered by small truck'; break;
      case 'SGN-TRUCK-2000': title = '2000kg truct'; description = 'Delivered by big truck'; break;
      case 'HAN-BIKE': title = 'Over speed'; description = 'Deliver within an hour'; break;
      case 'HAN-EXPRESS': title = 'Near delivery'; description = 'Super fast delivery only 30 minutes'; break;
      case 'HAN-POOL': title = 'Dirt cheap'; description = 'Save 20%, 2-hour delivery'; break;
      case 'HAN-SAMEDAY': title = '4-Hour'; description = 'Delivery within the city, committed in 4 hours'; break;
    }
    return {
      title: title,
      description: description
    };
  }
  visible_list_addressGHN: boolean = false
  getListAddressCustomer() {
    let type: number = this.delivery.type
    if (this.delivery.type >= 4) type = type + 2
    this.listAddressCustomer = this.vhQuerySales.getlocalAddresss().filter(item => item.type == type && item.id_customer == this.id_customer).sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date))
    if (this.listAddressCustomer.length) {
      this.infoCus.addressDelivery = this.renderAddress(this.listAddressCustomer[0])
      if (type == 1) this.changeWardGHN(true)
    }
  }
  renderAddress(info: any) {
    if (this.delivery.type == 4) {
      this.dataAddress = { ...info }
      return `${info.ReceiverAddress}, ${info.TenPhuongXa}, ${info.TenQuanHuyen}, ${info.TenTinhThanh}`
    } else if (this.delivery.type == 5) {
      this.dataAddress = { ...info }
      return `${info.RECEIVER_ADDRESS}, ${info.WARDS_NAME}, ${info.DISTRICT_NAME}, ${info.PROVINCE_NAME}`
    } else {
      this.dataAddress.DistrictID = info.district_id
      this.dataAddress.ProvinceName = info.province
      this.dataAddress.DistrictName = info.district
      this.dataAddress.WardName = info.ward
      this.dataAddress.address = info.address
      this.dataAddress.ProvinceID = info.province == 'H Ch Minh' ? 202 : 201
      if (this.delivery.type == 1) this.changeWardGHN(true)
      return `${info.hamlet || ''}${info.hamlet ? ' - ' : ''}${info.address}, ${info.ward}, ${info.district}, ${info.province}`
    }
  }
  onCloseListAddressGHN(event: any) {
    if (event && !event.add) {
      this.infoCus.addressDelivery = this.renderAddress(event)
      if (this.delivery.type != 3) this.changeWardGHN(true)
    } else if (event && event.add)
      this.visibleAddDelivery = true

    this.visible_list_addressGHN = false
  }
  visibleAddDelivery: boolean = false
  hideModalAddDelivery(event: any) {
    if (event)
      this.infoCus.addressDelivery = this.renderAddress(event)
    this.visibleAddDelivery = false
  }

  // --------------------------VNPOST--------------------------------------------
  listProvinceVnpost: Array<any> = []
  listDistrictVnpost: Array<any> = []
  listWardVnpost: Array<any> = []
  //------------------------------------Xa huyn tinh------------------------------------
  /**Ly tinh thanh ph */
  getProvinceVnpost() {
    this.isLoading = true
    this.vnPost.getProvinceVnpost().then((res: Array<any>) => {
      this.isLoading = false
      this.listProvinceVnpost = res
    })
  }
  /**
   * S kin thay i tnh => reset huyn x
   * @param event 
   */
  changeProvinceVnpost(event) {
    if (event) {
      this.listDistrictVnpost = []
      this.listWardVnpost = []
      this.dataAddress.ReceiverDistrictId = ''
      this.dataAddress.ReceiverWardId = ''
      let province = this.listProvinceVnpost.find(item => item.MaTinhThanh == event)
      this.dataAddress['TenTinhThanh'] = province.TenTinhThanh
      this.resultFeesVnpost = []
    }
  }
  /**Ly qun, huyn */
  getDistrictVnpost() {
    this.isLoading = true
    this.vnPost.getDistrictVnpost().then((res: Array<any>) => {
      this.listDistrictVnpost = res.filter(item => item.MaTinhThanh == this.dataAddress.ReceiverProvinceId)
      this.isLoading = false
    })
  }
  /**
  * S kin thay i huyn => reset x
  * @param event 
  */
  changeDistrictVnpost(event) {
    if (event) {
      this.listWardVnpost = []
      this.dataAddress.ReceiverWardId = ''
      let district = this.listDistrictVnpost.find(item => item.MaQuanHuyen == event)
      this.dataAddress['TenQuanHuyen'] = district.TenQuanHuyen
      this.resultFeesVnpost = []
    }
  }

  /**Ly phng, xa */
  getWardVnpost() {
    this.isLoading = true
    this.vnPost.getWardVnpost().then((res: Array<any>) => {
      this.listWardVnpost = res.filter(item => item.MaQuanHuyen == this.dataAddress.ReceiverDistrictId)
      this.isLoading = false
    })
  }
  /**
  * S kin thay i phng, xa
  * @param event 
  */
  changeWardVnpost(event) {
    if (event) {
      let ward = this.listWardVnpost.find(item => item.MaPhuongXa == event)
      this.dataAddress['TenPhuongXa'] = ward.TenPhuongXa
      this.resultFeesVnpost = []
    }
  }
  //------------------------------------Xa huyn tinh------------------------------------
  viewResultVnpost() {
    let data_deli = { ...this.dataAddress, CodAmountEvaluation: 0, OrderAmountEvaluation: 0, IsReceiverPayFreight: false }
    this.resultFeesVnpost = []
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      this.vnPost.getServiceNameVnpost(this.delivery.Token, this.vnPost.renderFormatService(this.delivery, data_deli, this.dataProduct, data_deli, this.delivery.CustomerCode)).then((result: Array<any>) => {
        result.forEach(item => {
          if (item.Success) {
            item.TenDichVu = this.renderNameServiceVnpost(item.MaDichVu, item.TongCuocBaoGomDVCT)
            this.resultFeesVnpost.push(item)

          }
        })
        if (this.resultFeesVnpost.length == 0) this.loadingDone = true
        this.vhComponent.hideLoading(0)
      })
    })
  }
  renderNameServiceVnpost(MaDichVu, TongCuocBaoGomDVCT) {
    switch (MaDichVu) {
      case 'EMS': return `${this.lang.translate('Chuyn phat nhanh')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'BK': return `${this.lang.translate('Chuyn phat thng')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'TMDT_EMS': return `TMDT_EMS - ${this.lang.translate('TMT-Chuyn pht nhanh EMS ')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'TMDT_BK': return `TMDT_BK - ${this.lang.translate('TMT-Chuyn pht tiu chun')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
      case 'TMDT_EMS_TK': return `TMDT_EMS_TK - ${this.lang.translate('TMT-Chuyn pht nhanh EMS tit kim (lin vng)')} - ${this.vhAlgorithm.vhcurrencyunit(TongCuocBaoGomDVCT)}`
    }
  }

  // -----------------------------------VIETTEL POST-----------------------------------
  //------------------------------------Xa huyn tinh------------------------------------
  /**Ly tinh thanh ph */
  getProvinceViettel() {
    this.isLoading = true
    this.viettelService.getProvinceViettel().then((res: any) => {
      this.isLoading = false
      if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
      else this.listProvinceViettel = res.data
    })
  }
  /**
   * S kin thay i tnh => reset huyn x
   * @param event 
   */
  changeProvinceViettel(event) {
    if (event) {
      this.listDistrictViettel = []
      this.listWardViettel = []
      this.dataAddress.RECEIVER_DISTRICT = ''
      this.dataAddress.RECEIVER_WARD = ''
      this.dataAddress.PROVINCE_NAME = this.listProvinceViettel.find(item => item.PROVINCE_ID == event).PROVINCE_NAME
    }
  }
  /**Ly qun, huyn */
  getDistrictViettel() {
    this.isLoading = true
    this.viettelService.getDistrictViettel(this.dataAddress.RECEIVER_PROVINCE).then((res: any) => {
      this.isLoading = false
      if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
      else this.listDistrictViettel = res.data.map(item => {
        item.DISTRICT_NAME = this.uppercaseFirstChar(item.DISTRICT_NAME)
        return item
      })
    })
  }
  /**
  * S kin thay i huyn => reset x
  * @param event 
  */
  changeDistrictViettel(event) {
    if (event) {
      this.listWardViettel = []
      this.dataAddress.RECEIVER_WARD = ''
      this.dataAddress.DISTRICT_NAME = this.listDistrictViettel.find(item => item.DISTRICT_ID == event).DISTRICT_NAME
    }
  }

  /**Ly phng, xa */
  getWardViettel() {
    this.isLoading = true
    this.viettelService.getWardViettel(this.dataAddress.RECEIVER_DISTRICT).then((res: any) => {
      this.isLoading = false
      if (res.error) this.vhComponent.alertMessageDesktop('error', res.message)
      else this.listWardViettel = res.data.map(item => {
        item.WARDS_NAME = this.uppercaseFirstChar(item.WARDS_NAME)
        return item
      })
    })
  }
  /**
  * S kin thay i phng, xa
  * @param event 
  */
  changeWardViettel(event) {
    if (event) {
      this.dataAddress.WARDS_NAME = this.listWardViettel.find(item => item.WARDS_ID == event).WARDS_NAME
      this.getAllServiceVietel()
    }
  }
  getAllServiceVietel() {
    let MONEY_COLLECTION = this.MONEY_COLLECTION.getRawValue()
    let PRODUCT_PRICE = this.PRODUCT_PRICE.getRawValue()
    
    
    setTimeout(() => {
      if (this.dataAddress.RECEIVER_WARD && this.dataProduct.PRODUCT_WEIGHT && (MONEY_COLLECTION && MONEY_COLLECTION >= 0) && (PRODUCT_PRICE && PRODUCT_PRICE >= 0)) {
        this.viettelService.getServiceViettel(this.delivery.token, { ...this.dataAddress, ...this.dataProduct, SENDER_DISTRICT: this.delivery.districtId, SENDER_PROVINCE: this.delivery.provinceId, PRODUCT_PRICE, MONEY_COLLECTION }).then((result: any) => {
          if (result.length) this.listServiceViettel = result
          else this.vhComponent.alertMessageDesktop('error', result.message)
          
        })
      }
    }, 100)
  }
  serviceSelectedViettel: any = {}

  changeServiceViettel(event) {
    this.serviceSelectedViettel = {}
    this.listServiceAddViettel = this.listServiceViettel.find(item => item.MA_DV_CHINH == event).EXTRA_SERVICE
    this.getPriceOfServiceSelected()
  }

  getPriceSpecially() {
    let ORDER_SERVICE_ADD: string = ''
    this.listServiceAddViettel.forEach(item => {
      if (item.checked) ORDER_SERVICE_ADD += `${item.SERVICE_CODE},`
    })
    ORDER_SERVICE_ADD = ORDER_SERVICE_ADD.slice(0, ORDER_SERVICE_ADD.lastIndexOf(','))
    this.dataAddress.ORDER_SERVICE_ADD = ORDER_SERVICE_ADD
    this.getPriceOfServiceSelected()
  }

  getPriceOfServiceSelected() {
    let MONEY_COLLECTION = this.MONEY_COLLECTION.getRawValue()
    let PRODUCT_PRICE = this.PRODUCT_PRICE.getRawValue()
    this.viettelService.getPriceSpeciallyViettel(this.delivery.token, { ...this.dataAddress, ...this.dataProduct, SENDER_DISTRICT: this.delivery.districtId, SENDER_PROVINCE: this.delivery.provinceId, PRODUCT_PRICE, MONEY_COLLECTION }).then((result: any) => {
      if (result.error) this.vhComponent.alertMessageDesktop('error', result.message)
      else this.serviceSelectedViettel = result.data
    })
  }
  //Vit hoa ch cai u cua mi t
  uppercaseFirstChar(str: string) {
    let array = str.toLowerCase().split(' ');
    let newStr = '';
    array.forEach((item) => {
      newStr += item.charAt(0).toUpperCase() + item.slice(1) + ' ';
    });
    return newStr
  }
  // -----------------------------------VIETTEL POST-----------------------------------
}
