import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhQuerySales, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { AhamoveService } from 'src/app/sales/menu/manage/delivery/services/ahamoveService/ahamove.service';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { GhtkService } from 'src/app/sales/menu/manage/delivery/services/ghtkService/ghtk.service';
import { LanguageService } from 'src/app/sales/services/language.service';
const GHNTOKEN: string = '656776fd-1e78-11eb-aaa9-3e267f98f494'
@Component({
  selector: 'app-add-address-delivery',
  templateUrl: './add-address-delivery.component.html',
  styleUrls: ['./add-address-delivery.component.scss']
})
export class AddAddressDeliveryComponent implements OnInit {

  @Input('delivery') delivery;
  @Input('visibleAddDelivery') visibleAddDelivery = false
  @Output('hideModalAddDelivery') hideModalAddDelivery = new EventEmitter<any>()
  @Input('id_customer') id_customer: string
  isLoading: boolean = false
  createLoading: boolean = false
  listProvinceGHN: Array<any> = []
  listDistrictGHN: Array<any> = []
  listWardGHN: Array<any> = []
  serviceGHN: Array<any> = []
  dataAddress: any = { ProvinceID: '', ProvinceName: '', DistrictID: '', DistrictName: '', WardCode: '', WardName: '' }
  dataProduct: any = { height: '', weight: '', length: '', width: '' }
  constructor(
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private ghnService: GhnService,
    private ghtkService: GhtkService,
    private ahamoveService: AhamoveService,
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
  }
  messageError() {
    this.vhComponent.alertMessageDesktop('error', this.lang.translate('An error occured while getting transport'))
  }
  hideModal() {
    this.visibleAddDelivery = false
    this.hideModalAddDelivery.emit(false)
  }
  getProvinceGHN() {
    this.isLoading = true
    this.ghnService.getProvinceGhn(GHNTOKEN).then((res: any) => {
      this.isLoading = false
      if (res.code == 200 && this.delivery.type != 3)
        this.listProvinceGHN = res.data
      else if (res.code == 200 && this.delivery.type == 3)
        this.listProvinceGHN = res.data.filter(item => item.ProvinceID == 201 || item.ProvinceID == 202)

    }).catch(err => { this.messageError(); this.isLoading = false })
  }
  /**
   * S kin thay i tnh => reset huyn x
   * @param event 
   */
  changeProvinceGHN(event: any) {
    if (event) {
      this.listDistrictGHN = []
      this.listWardGHN = []
      this.dataAddress['WardCode'] = ''
      this.dataAddress['DistrictID'] = ''
      let province = this.listProvinceGHN.find(item => item.ProvinceID == event)
      this.dataAddress['ProvinceName'] = province.ProvinceName
    }
  }
  /**
   * Ly danh sch huyn
   */
  getDistrictGHN() {
    this.isLoading = true
    this.ghnService.getDistrictGhn(GHNTOKEN, this.dataAddress['ProvinceID']).then((res: any) => {
      this.isLoading = false
      if (res.code == 200) this.listDistrictGHN = res.data
    }).catch(err => { this.messageError(); this.isLoading = false })
  }
  /**
   * S kin thay i a ch huyn => reset x
   * @param event 
   */
  changeDistrictGHN(event: any) {
    if (event) {
      this.dataAddress['WardCode'] = ''
      this.dataAddress['DistrictID'] = event
      let district = this.listDistrictGHN.find(item => item.DistrictID == event)
      this.dataAddress['DistrictName'] = district.DistrictName
    }
  }
  /**
   * Ly danh sch x, phng
   */
  getWardGHN() {
    this.isLoading = true
    this.ghnService.getWardGhn(GHNTOKEN, this.dataAddress['DistrictID']).then((res: any) => {
      this.isLoading = false
      if (res.code == 200) this.listWardGHN = res.data
    }).catch(err => { this.messageError(); this.isLoading = false })
  }
  /**
   * Sau khi chn xong x phng, dng api get v cc dch v h tr(i b, my bay...)
   * @param event 
   */
  changeWardGHN(event: any) {
    if (event && this.delivery.type == 1) {
      let json: any = {
        shop_id: this.delivery.shop_id,
        from_district_id: this.delivery.district_id,
        to_district_id: this.dataAddress.DistrictID
      }
      this.ghnService.getServiceDelivery(this.delivery.token, json).then((res: any) => {
        
        this.serviceGHN = res.data.filter(item => {
          return item.service_type_id == 1 || item.service_type_id == 2
        });
      }).catch(err => { this.messageError() })
    }
    let ward = this.listWardGHN.find(item => item.WardCode == event)
    this.dataAddress['WardName'] = ward.WardName
  }
  createAddress() {
    this.createLoading = true
    let json = {}
    if (this.delivery.type == 1) json = this.exportAddressGHN()
    else if (this.delivery.type == 2) json = this.exportAddressGHTK()
    else if (this.delivery.type == 3) json = this.exportaddressAhamove()
    this.vhQuerySales.addAddress(json).then(res => {
      if (res) {
        this.vhComponent.alertMessageDesktop('success', this.lang.translate('This address has been created successfully'))
        this.hideModalAddDelivery.emit(json)
      }
    }).catch(err => this.vhComponent.alertMessageDesktop('error', err)).finally(() => this.createLoading = false)
  }
  exportAddressGHN() {
    return {
      id_customer: this.id_customer,
      type: this.delivery.type,
      province_id: this.dataAddress.ProvinceID,
      province: this.dataAddress.ProvinceName,
      district_id: this.dataAddress.DistrictID,
      district: this.dataAddress.DistrictName,
      ward_id: this.dataAddress.WardCode,
      ward: this.dataAddress.WardName,
      address: this.dataAddress.address,
      date: new Date().toISOString(),
    }
  }
  exportAddressGHTK() {
    return {
      id_customer: this.id_customer,
      type: this.delivery.type,
      province: this.dataAddress.ProvinceName,
      district: this.dataAddress.DistrictName,
      ward: this.dataAddress.WardName,
      address: this.dataAddress.address,
      hamlet: this.dataAddress.hamlet,
      date: new Date().toISOString(),
    }
  }
  exportaddressAhamove() {
    return {
      id_customer: this.id_customer,
      type: this.delivery.type,
      province: this.dataAddress.ProvinceName,
      district: this.dataAddress.DistrictName,
      ward: this.dataAddress.WardName,
      address: this.dataAddress.address,
      date: new Date().toISOString(),
    }
  }
}
